}

.navbar { /* Men√º oben fixed */
  overflow: hidden;
  position: fixed;
  z-index:1;
  top: 0;

}
.navbar a {
  float: left;
  display: block;

  text-align: center;
  padding: 22px 22px;
  text-decoration: none;
  font-size: 18px;
  font-family: fantasy;
  color:#6600cc;

}


.navbar a:hover {
  background: #9999ff	;
  color: #4dffd2;
  border-radius: 999px;
  border-color: #4dffd2;
  opacity: 0.3;
}



